
const base_url = 'http://localhost:8080/api/tournament';


export const createTournament = async ({ tournament_name, host_email, host_telegram, max_participants, tx_hash , tournament_id}) => {
    const data = {
        tournament_name, host_email, host_telegram, max_participants, tx_hash, tournament_id
    };
    const headers = {
        authorization: localStorage.getItem("auth_token") ?? "",
        'Content-Type': 'application/json'
    }
   const res = await fetch(base_url, {
        method: 'POST',
        body: JSON.stringify(data),
        headers
    }, {});
    return res;
}